import IntegrationIcons from '@/assets/integration_icons';
import { Component, Event, EventEmitter, Host, h } from '@stencil/core';
import { TAuthNavigation } from '../ir-auth/auth.types';

@Component({
  tag: 'ir-signin',
  styleUrl: 'ir-signin.css',
  scoped: true,
})
export class IrSignin {
  @Event() navigate: EventEmitter<TAuthNavigation>;
  renderFBIcon() {
    return <span slot="left-icon">{IntegrationIcons['001']}</span>;
  }
  render() {
    return (
      <Host>
        <h1 class="title">Log in to your account</h1>
        <p class="Supporting-text mb-8 ">Welcome back! Please enter your details.</p>
        <div class="space-y-4 mb-8 ">
          <fieldset class="mb-5">
            {/* <label htmlFor="email">Email</label> */}
            <ir-input autofocus inputId="email" placeholder="Enter your email"></ir-input>
          </fieldset>
          <fieldset>
            {/* <label htmlFor="password" class="mb-2">
              Password
            </label> */}
            <ir-input inputId="password" type="password" placeholder="Enter your password"></ir-input>
          </fieldset>
          <div class="my-6 flex justify-end">
            <ir-button variants="ghost-primary" label="Forgot password"></ir-button>
          </div>
          <ir-button class="w-full" label="Sign in" size="md"></ir-button>
          {/* <ir-button class="w-full" variants="outline" label="Sign in with Google" haveLeftIcon>
            <span slot="left-icon">{IntegrationIcons.google}</span>
          </ir-button> */}
          <ir-button class="w-full" variants="outline" label="Sign in with Facebook" haveLeftIcon>
            {this.renderFBIcon()}
          </ir-button>
        </div>
        <div class="flex items-center justify-center">
          <p class="dont-have-an-account">Don't have an account?</p>
          <ir-button label="Sign up" variants="ghost-primary" onButtonClick={() => this.navigate.emit('register')}></ir-button>
        </div>
      </Host>
    );
  }
}
